.popup{
    
    position: fixed;  
    width: 100%;  
    height: 100%;  
    top: 0;  
    left: 0;  
    right: 0;  
    bottom: 0;  
    margin: auto;  
    background-color: rgba(0,0,0, 0.5);  
  
}
.inner-popup{
    position: absolute;  
    width: 30%;
    height: 200px;
    left: 35%;  
    right: 35%;  
    top: 25%;  
    bottom: 25%;  
    border-radius: 20px;  
    background: white;  
  
}